// src/components/DocumentForm.js
import React from 'react';
import {
  Grid,
  Paper,
  Typography,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  Checkbox,
} from '@mui/material';

const DocumentForm = ({
  document,
  analysts,
  handleAllocationChange,
}) => {
  const handleRadioChange = (e) => {
    const value = e.target.value;
    if (value === 'all') {
      handleAllocationChange(document.id, ['all']);
    } else if (value === 'specific') {
      if (document.allocatedTo.includes('all')) {
        const confirmSwitch = window.confirm(
          'Switching to specific analysts will remove the current "All Analysts" allocation. Do you want to proceed?'
        );
        if (confirmSwitch) {
          handleAllocationChange(document.id, []);
        }
      } else {
        handleAllocationChange(document.id, []); // Reset allocations to specific analysts
      }
    }
  };

  const handleCheckboxChange = (e, analystId) => {
    const { checked } = e.target;
    let newAllocatedTo = [...document.allocatedTo];
    if (checked) {
      // Prevent adding duplicate IDs
      if (!newAllocatedTo.includes(analystId)) {
        newAllocatedTo.push(analystId);
      }
    } else {
      newAllocatedTo = newAllocatedTo.filter((id) => id !== analystId);
    }
    handleAllocationChange(document.id, newAllocatedTo);
  };

  const selectedCount = document.allocatedTo.filter(id => id !== 'all').length;

  return (
    <Grid item xs={12}>
      <Paper elevation={2} sx={{ p: 2 }}>
        <Typography variant="h6">{document.name}</Typography>
        <Typography variant="body2" color="textSecondary">
          {document.type.toUpperCase()}
        </Typography>
        <FormControl component="fieldset" sx={{ mt: 2 }}>
          <FormLabel component="legend">Allocate To</FormLabel>
          <RadioGroup
            row
            value={document.allocatedTo.includes('all') ? 'all' : 'specific'}
            onChange={handleRadioChange}
          >
            <FormControlLabel
              value="all"
              control={<Radio aria-label="Allocate to all analysts" />}
              label="All Analysts"
            />
            <FormControlLabel
              value="specific"
              control={<Radio aria-label="Allocate to specific analysts" />}
              label="Specific Analysts"
            />
          </RadioGroup>
        </FormControl>
        {document.allocatedTo.includes('all') ? null : (
          <>
            <FormControl component="fieldset" sx={{ mt: 2 }}>
              <FormLabel component="legend">Select Analysts</FormLabel>
              <Typography variant="caption" color="textSecondary" sx={{ mb: 1 }}>
                {selectedCount > 0
                  ? `${selectedCount} Analyst${selectedCount > 1 ? 's' : ''} Selected`
                  : 'No Analysts Selected'}
              </Typography>
              <Grid container>
                {analysts.map((analyst, index) => (
                  <Grid item xs={12} sm={6} md={4} key={analyst.id}>
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={document.allocatedTo.includes(analyst.id)}
                          onChange={(e) => handleCheckboxChange(e, analyst.id)}
                          aria-label={`Allocate to Analyst ${index + 1}`}
                        />
                      }
                      label={`Analyst ${index + 1}`}
                    />
                  </Grid>
                ))}
              </Grid>
            </FormControl>
          </>
        )}
      </Paper>
    </Grid>
  );
};

export default React.memo(DocumentForm);
