// components/AnalystForm.js
import React from 'react';
import { Grid, TextField, Button, Paper } from '@mui/material';

const AnalystForm = ({ index, analyst, setAnalysts, removeAnalyst, analysts }) => {
  const handleChange = (e) => {
    const newAnalysts = [...analysts];
    newAnalysts[index][e.target.name] = e.target.value;
    setAnalysts(newAnalysts);
  };

  return (
    <Grid item xs={12}>
      <Paper elevation={2} sx={{ p: 2 }}>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} md={5}>
            <TextField
              label={`Role of Analyst ${index + 1}`}
              variant="outlined"
              fullWidth
              name="role"
              value={analyst.role}
              onChange={handleChange}
              required
            />
          </Grid>
          <Grid item xs={12} md={5}>
            <TextField
              label={`Goal of Analyst ${index + 1}`}
              variant="outlined"
              fullWidth
              name="goal"
              value={analyst.goal}
              onChange={handleChange}
              required
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <Button
              variant="outlined"
              color="error"
              fullWidth
              onClick={() => removeAnalyst(index)}
            >
              Remove
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Grid>
  );
};

export default AnalystForm;
