// components/AttendeeForm.js
import React from 'react';
import { Grid, TextField, Button, Paper } from '@mui/material';

const AttendeeForm = ({
  index,
  attendee,
  setAttendees,
  removeAttendee,
  attendees,
}) => {
  const handleChange = (e) => {
    const newAttendees = [...attendees];
    newAttendees[index][e.target.name] = e.target.value;
    setAttendees(newAttendees);
  };

  return (
    <Grid item xs={12}>
      <Paper elevation={2} sx={{ p: 2 }}>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} md={4}>
            <TextField
              label={`Name of Attendee ${index + 1}`}
              variant="outlined"
              fullWidth
              name="name"
              value={attendee.name}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} md={4}>
            <TextField
              label={`Role of Attendee ${index + 1}`}
              variant="outlined"
              fullWidth
              name="role"
              value={attendee.role}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} md={4}>
            <Button
              variant="outlined"
              color="error"
              fullWidth
              onClick={() => removeAttendee(index)}
            >
              Remove
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Grid>
  );
};

export default AttendeeForm;
