// src/App.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Button,
  TextField,
  Typography,
  Container,
  Box,
  Paper,
  Tabs,
  Tab,
  AppBar,
  Grid,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  LinearProgress,
  ThemeProvider,
  createTheme,
  CssBaseline,
  Toolbar,
  Switch,
  FormControlLabel,
} from '@mui/material';
import {
  SaveAlt,
  UploadFile,
} from '@mui/icons-material';
import { v4 as uuidv4 } from 'uuid';

// Import PDF.js
import * as pdfjsLib from 'pdfjs-dist/webpack';

// Import custom components
import AnalystForm from './components/AnalystForm';
import AttendeeForm from './components/AttendeeForm';
import DocumentForm from './components/DocumentForm';

// Sleep function to introduce delays
const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

function App() {
  // State variables
  const [analysts, setAnalysts] = useState([
    { id: uuidv4(), role: '', goal: '' }
  ]);
  const [managerRole, setManagerRole] = useState('');
  const [managerGoal, setManagerGoal] = useState('');
  const [documents, setDocuments] = useState([]); // State for documents
  const [output, setOutput] = useState('');
  const [analystOutputs, setAnalystOutputs] = useState([]);
  const [value, setValue] = useState(0);
  const [status, setStatus] = useState('');
  const [model, setModel] = useState('gpt-4o-mini');
  const [apiKey, setApiKey] = useState('');
  const [loading, setLoading] = useState(false);

  // New state variables for meeting preparation
  const [meetingLength, setMeetingLength] = useState('');
  const [attendees, setAttendees] = useState([{ name: '', role: '' }]);
  const [meetingGoal, setMeetingGoal] = useState('');

  // Dark mode state with persistence
  const [darkMode, setDarkMode] = useState(() => {
    const savedMode = localStorage.getItem('darkMode');
    return savedMode ? JSON.parse(savedMode) : false;
  });

  // Save dark mode preference to localStorage
  useEffect(() => {
    localStorage.setItem('darkMode', JSON.stringify(darkMode));
  }, [darkMode]);

  // Theme configuration
  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
    },
  });

  // Functions for configuration file
  const handleSaveConfig = () => {
    const config = {
      analysts,
      managerRole,
      managerGoal,
      meetingLength,
      attendees,
      meetingGoal,
      // documents
    };
    const blob = new Blob([JSON.stringify(config, null, 2)], {
      type: 'application/json',
    });
    const link = document.createElement('a');
    link.href = window.URL.createObjectURL(blob);
    link.download = 'config.json';
    link.click();
  };

  const handleLoadConfig = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const config = JSON.parse(e.target.result);
          // Ensure all analyst IDs are preserved and unique
          const loadedAnalysts = config.analysts
            ? config.analysts.map((analyst) => ({
                id: analyst.id || uuidv4(),
                role: analyst.role || '',
                goal: analyst.goal || '',
              }))
            : [{ id: uuidv4(), role: '', goal: '' }];
          
          setAnalysts(loadedAnalysts);
          setManagerRole(config.managerRole || '');
          setManagerGoal(config.managerGoal || '');
          setMeetingLength(config.meetingLength || '');
          setAttendees(config.attendees || [{ name: '', role: '' }]);
          setMeetingGoal(config.meetingGoal || '');
          setDocuments(config.documents || []);
          setOutput('');
          setAnalystOutputs([]);
        } catch (error) {
          alert('Invalid configuration file.');
          console.error('Error parsing config file:', error);
        }
      };
      reader.readAsText(file);
    }
  };

  const addAnalyst = () => {
    setAnalysts([...analysts, { id: uuidv4(), role: '', goal: '' }]);
  };

  const removeAnalyst = (id) => {
    console.log(`Attempting to remove analyst with ID: ${id}`);

    // Prevent removing the last analyst
    if (analysts.length === 1) {
      alert('At least one analyst is required.');
      return;
    }

    setAnalysts((prevAnalysts) => {
      const updatedAnalysts = prevAnalysts.filter((analyst) => analyst.id !== id);
      console.log("Updated Analysts:", updatedAnalysts); // Log updated analysts
      return updatedAnalysts;
    });

    setDocuments((prevDocs) => {
      const updatedDocs = prevDocs.map((doc) => ({
        ...doc,
        allocatedTo: doc.allocatedTo.filter((allocId) => allocId !== id),
      }));
      console.log("Updated Documents:", updatedDocs); // Log updated docs
      return updatedDocs;
    });
  };

  // Functions to add and remove attendees
  const addAttendee = () => {
    setAttendees([...attendees, { name: '', role: '' }]);
  };

  const removeAttendee = (index) => {
    const newAttendees = [...attendees];
    newAttendees.splice(index, 1);
    setAttendees(newAttendees);
  };

  // Handle file upload for .txt and .pdf
  const handleFileUpload = async (e) => {
    const files = Array.from(e.target.files);
    if (files.length === 0) return;

    const newDocuments = [];

    for (let file of files) {
      const fileType = file.type;
      let type = '';
      if (fileType === 'text/plain') {
        type = 'txt';
      } else if (fileType === 'application/pdf') {
        type = 'pdf';
      } else {
        alert(`Unsupported file type: ${file.name}`);
        continue;
      }

      const id = uuidv4(); // Unique ID

      // Extract text content
      let content = '';
      if (type === 'txt') {
        try {
          const reader = new FileReader();
          const text = await new Promise((resolve, reject) => {
            reader.onload = () => resolve(reader.result);
            reader.onerror = () => reject(reader.error);
            reader.readAsText(file);
          });
          content = text;
        } catch (error) {
          console.error('Error reading TXT file:', error);
          alert(`Failed to read file: ${file.name}`);
          continue;
        }
      } else if (type === 'pdf') {
        try {
          const arrayBuffer = await file.arrayBuffer();
          const pdf = await pdfjsLib.getDocument({ data: arrayBuffer }).promise;
          let textContent = '';
          for (let pageNum = 1; pageNum <= pdf.numPages; pageNum++) {
            const page = await pdf.getPage(pageNum);
            const text = await page.getTextContent();
            const pageText = text.items.map((item) => item.str).join(' ');
            textContent += pageText + '\n';
          }
          content = textContent;
        } catch (error) {
          console.error('Error parsing PDF:', error);
          alert(`Failed to parse PDF file: ${file.name}`);
          continue;
        }
      }

      newDocuments.push({
        id,
        file,
        name: file.name,
        type,
        allocatedTo: ['all'], // Default allocation
        content,
      });
    }

    setDocuments((prev) => [...prev, ...newDocuments]);
    e.target.value = null; // Reset the input
  };

  const handleAllocationChange = (docId, allocatedTo) => {
    setDocuments((prevDocs) =>
      prevDocs.map((doc) =>
        doc.id === docId ? { ...doc, allocatedTo } : doc
      )
    );
  };

  const handleReset = () => {
    setAnalysts([{ id: uuidv4(), role: '', goal: '' }]);
    setManagerRole('');
    setManagerGoal('');
    setDocuments([]);
    setOutput('');
    setAnalystOutputs([]);
    setMeetingLength('');
    setAttendees([{ name: '', role: '' }]);
    setMeetingGoal('');
    setStatus('');
    setValue(0);
    setApiKey('');
  };

  // Function to make API requests with retry logic
  const makeApiRequest = async (payload) => {
    const headers = { Authorization: `Bearer ${apiKey}` };
    try {
      const response = await axios.post('https://api.openai.com/v1/chat/completions', payload, { headers });
      return response;
    } catch (error) {
      if (error.response && error.response.status === 429) {
        // Rate limit exceeded, handle according to Retry-After header
        const retryAfter = error.response.headers['retry-after']
          ? parseInt(error.response.headers['retry-after'], 10) * 1000
          : 3000; // Default to 3 seconds
        console.warn(`Rate limit exceeded. Retrying in ${retryAfter / 1000} seconds...`);
        await sleep(retryAfter);
        throw error; // Let the caller handle the retry
      } else {
        throw error; // Propagate other errors
      }
    }
  };

  // Updated handleSubmit function implementing sequential requests with delays
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Input validation
    if (!apiKey) {
      alert('Please enter your OpenAI API key.');
      return;
    }

    if (analysts.some((analyst) => !analyst.role || !analyst.goal)) {
      alert('Please fill out all analyst roles and goals.');
      return;
    }

    if (!managerRole || !managerGoal) {
      alert('Please fill out the manager role and goal.');
      return;
    }

    if (documents.length === 0) {
      alert('Please upload at least one document.');
      return;
    }

    // Ensure all documents have allocations
    const unallocatedDocs = documents.filter(
      (doc) =>
        !doc.allocatedTo.includes('all') &&
        doc.allocatedTo.length === 0
    );
    if (unallocatedDocs.length > 0) {
      alert('Please allocate all documents to specific analysts or to all analysts.');
      return;
    }

    setLoading(true);
    setStatus('Starting the request...');
    setAnalystOutputs([]);
    setOutput('');

    // Prepare allocations
    // Create a map: analystId -> [documents]
    const analystDocsMap = analysts.reduce((acc, analyst) => {
      acc[analyst.id] = [];
      return acc;
    }, {});

    // Add documents allocated to all analysts to each analyst's list
    const allDocuments = documents.filter((doc) => doc.allocatedTo.includes('all'));

    analysts.forEach((analyst) => {
      analystDocsMap[analyst.id] = [...allDocuments];
    });

    // Add documents allocated specifically to analysts
    documents.forEach((doc) => {
      if (!doc.allocatedTo.includes('all')) {
        doc.allocatedTo.forEach((analystId) => {
          if (analystDocsMap[analystId]) {
            analystDocsMap[analystId].push(doc);
          }
        });
      }
    });

    console.log('Analyst Documents Map:', analystDocsMap); // Debugging

    setStatus('Processing Analysts...');
    try {
      const analystResults = [];

      for (let idx = 0; idx < analysts.length; idx++) {
        const analyst = analysts[idx];
        const allocatedDocs = analystDocsMap[analyst.id];

        if (allocatedDocs.length === 0) {
          analystResults.push({
            analystId: analyst.id,
            index: idx + 1,
            output: 'No documents allocated.',
          });
          continue;
        }

        // Combine all contents
        const combinedInfo = allocatedDocs.map((doc) => doc.content).join('\n');

        const payload = {
          model: model,
          messages: [
            {
              role: 'system',
              content: `You are a ${analyst.role}. Your goal is to ${analyst.goal}.`,
            },
            { role: 'user', content: `Here is the information:\n${combinedInfo}` },
          ],
        };

        try {
          const response = await makeApiRequest(payload);

          if (
            response &&
            response.data &&
            response.data.choices &&
            response.data.choices.length > 0
          ) {
            const analystOutput = response.data.choices[0].message.content;
            analystResults.push({
              analystId: analyst.id,
              index: idx + 1,
              output: analystOutput,
            });
          } else {
            console.error(`Invalid response structure for Analyst ${idx + 1}`, response);
            analystResults.push({
              analystId: analyst.id,
              index: idx + 1,
              output: 'Invalid response from API',
            });
          }
        } catch (error) {
          if (error.response && error.response.status === 429) {
            // After retries, still rate limited
            analystResults.push({
              analystId: analyst.id,
              index: idx + 1,
              output: 'Rate limit exceeded. Please try again later.',
            });
          } else {
            console.error(`Error with Analyst ${idx + 1}:`, error.response ? error.response.data : error.message);
            analystResults.push({
              analystId: analyst.id,
              index: idx + 1,
              output: 'Error generating output',
            });
          }
        }

        // Introduce a delay between submissions to prevent hitting rate limits
        const delayBetweenRequests = 3000; // 3 seconds
        setStatus(`Processed Analyst ${idx + 1} of ${analysts.length}. Waiting ${delayBetweenRequests / 1000} seconds before next request...`);
        await sleep(delayBetweenRequests);
      }

      setAnalystOutputs(analystResults);

      console.log('Analyst Outputs:', analystResults); // Debugging

      // Prepare manager's input using analystResults directly
      let managerInput = 'Here are the outputs from your analysts:\n';
      analystResults.forEach((analystOutput) => {
        managerInput += `${analystOutput.index}. ${analystOutput.output}\n`;
      });

      console.log('Manager Input:', managerInput); // Debugging

      // Include the meeting preparation details in the manager's system prompt
      let meetingDetails = `You are preparing for a meeting.\n`;
      if (meetingLength) {
        meetingDetails += `Meeting Length: ${meetingLength} minutes.\n`;
      }
      if (attendees.length > 0) {
        meetingDetails += `The Attendees and their roles are:\n`;
        attendees.forEach((attendee, idx) => {
          meetingDetails += `${idx + 1}. ${attendee.name} - ${attendee.role}\n`;
        });
      }
      if (meetingGoal) {
        meetingDetails += `The goal of the Meeting is to ${meetingGoal}\n`;
      }

      setStatus('Processing Manager...');
      try {
        const managerPayload = {
          model: model,
          messages: [
            {
              role: 'system',
              content: `You are a manager. You ${managerRole}. Your goal is to use the information provided by your analysts to ${managerGoal}.\n${meetingDetails}`,
            },
            { role: 'user', content: managerInput },
          ],
        };

        const managerResponse = await makeApiRequest(managerPayload);

        if (
          managerResponse &&
          managerResponse.data &&
          managerResponse.data.choices &&
          managerResponse.data.choices.length > 0
        ) {
          const managerOutput = managerResponse.data.choices[0].message.content;
          setOutput(managerOutput);
        } else {
          console.error('Invalid response structure for Manager', managerResponse);
          setOutput('Invalid response from API');
        }

        setValue(0); // Switch to the output tab
      } catch (error) {
        if (error.response && error.response.status === 429) {
          console.error('Rate limit exceeded for Manager. Please try again later.');
          setOutput('Rate limit exceeded. Please wait and try again.');
        } else {
          console.error(
            'Error with Manager:',
            error.response ? error.response.data : error.message
          );
          setOutput('Error generating manager output');
        }
      }

      setStatus('Completed');
      setLoading(false);
    } catch (error) {
      console.error('Error processing analysts:', error);
      setLoading(false);
      setStatus('Error occurred during processing.');
    }
  };

  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  };

  const availableModels = ['gpt-4o-mini', 'gpt-3.5-turbo'];

  const handleDarkModeToggle = () => {
    setDarkMode(!darkMode);
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container maxWidth="lg">
        <AppBar position="static" color="default" sx={{ mb: 4 }}>
          <Toolbar>
            <Typography variant="h6" sx={{ flexGrow: 1 }}>
              Agent Workflow
            </Typography>
            <FormControlLabel
              control={
                <Switch checked={darkMode} onChange={handleDarkModeToggle} />
              }
              label="Dark Mode"
            />
          </Toolbar>
        </AppBar>

        <Paper elevation={3} sx={{ p: { xs: 2, md: 4 } }}>
          <form onSubmit={handleSubmit}>
            <Grid container spacing={2}>
              {/* Settings Section */}
              <Grid item xs={12}>
                <Typography variant="h4" gutterBottom>
                  Settings
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  label="OpenAI API Key"
                  variant="outlined"
                  fullWidth
                  type="password"
                  value={apiKey}
                  onChange={(e) => setApiKey(e.target.value)}
                  helperText="Your API key will be used to process requests."
                  required
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <FormControl variant="outlined" fullWidth required>
                  <InputLabel>Model</InputLabel>
                  <Select
                    label="Model"
                    value={model}
                    onChange={(e) => setModel(e.target.value)}
                  >
                    {availableModels.map((modelOption) => (
                      <MenuItem key={modelOption} value={modelOption}>
                        {modelOption}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>

              {/* Analysts Section */}
              <Grid item xs={12}>
                <Typography variant="h4" gutterBottom>
                  Analysts
                </Typography>
              </Grid>
              {analysts.map((analyst, index) => (
                <AnalystForm
                  key={analyst.id}
                  index={index}
                  analyst={analyst}
                  setAnalysts={setAnalysts}
                  removeAnalyst={removeAnalyst}
                  analysts={analysts}
                />
              ))}
              <Grid item xs={12}>
                <Button variant="contained" onClick={addAnalyst} fullWidth>
                  Add Analyst
                </Button>
              </Grid>

              {/* Manager Section */}
              <Grid item xs={12}>
                <Typography variant="h4" gutterBottom>
                  Manager
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  label="You are a manager. You..."
                  variant="outlined"
                  fullWidth
                  value={managerRole}
                  onChange={(e) => setManagerRole(e.target.value)}
                  required
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  label="Your goal is to use the information provided by your analysts to..."
                  variant="outlined"
                  fullWidth
                  value={managerGoal}
                  onChange={(e) => setManagerGoal(e.target.value)}
                  required
                />
              </Grid>

              {/* Meeting Preparation Section */}
              <Grid item xs={12}>
                <Typography variant="h4" gutterBottom>
                  Meeting Preparation
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  label="Length of Meeting (minutes)"
                  variant="outlined"
                  fullWidth
                  value={meetingLength}
                  onChange={(e) => setMeetingLength(e.target.value)}
                  type="number"
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  label="The goal of the meeting is to..."
                  variant="outlined"
                  fullWidth
                  value={meetingGoal}
                  onChange={(e) => setMeetingGoal(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <Typography variant="h5" gutterBottom>
                  Attendees
                </Typography>
              </Grid>
              {attendees.map((attendee, index) => (
                <AttendeeForm
                  key={index}
                  index={index}
                  attendee={attendee}
                  setAttendees={setAttendees}
                  removeAttendee={removeAttendee}
                  attendees={attendees}
                />
              ))}
              <Grid item xs={12}>
                <Button variant="contained" onClick={addAttendee} fullWidth>
                  Add Attendee
                </Button>
              </Grid>

              {/* Information for Analysts */}
              <Grid item xs={12}>
                <Typography variant="h4" gutterBottom>
                  Information for Analysts
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Button
                  variant="contained"
                  component="label"
                  startIcon={<UploadFile />}
                  sx={{ mr: 2 }}
                >
                  Upload .txt/.pdf Files
                  <input
                    type="file"
                    hidden
                    accept=".txt,application/pdf"
                    multiple
                    onChange={handleFileUpload}
                  />
                </Button>
                <Button
                  variant="contained"
                  component="label"
                  startIcon={<UploadFile />}
                >
                  Load Config File
                  <input
                    type="file"
                    hidden
                    accept=".json"
                    onChange={handleLoadConfig}
                  />
                </Button>
              </Grid>
              <Grid item xs={12}>
                <Grid container spacing={2}>
                  {documents.map((doc) => (
                    <DocumentForm
                      key={doc.id}
                      document={doc}
                      analysts={analysts}
                      handleAllocationChange={handleAllocationChange}
                    />
                  ))}
                </Grid>
              </Grid>

              {/* Action Buttons */}
              <Grid item xs={12} md={4}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  fullWidth
                  disabled={loading}
                >
                  Submit
                </Button>
              </Grid>
              <Grid item xs={12} md={4}>
                <Button
                  type="button" // Prevent form submission
                  variant="outlined"
                  color="secondary"
                  fullWidth
                  onClick={handleReset}
                  disabled={loading}
                >
                  Reset
                </Button>
              </Grid>
              <Grid item xs={12} md={4}>
                <Button
                  type="button" // Prevent form submission
                  variant="contained"
                  color="success"
                  fullWidth
                  startIcon={<SaveAlt />}
                  onClick={handleSaveConfig}
                  disabled={loading}
                >
                  Save Config
                </Button>
              </Grid>
            </Grid>
          </form>
          {loading && (
            <Box sx={{ width: '100%', mt: 2 }}>
              <LinearProgress />
              <Typography variant="body1" align="center" sx={{ mt: 1 }}>
                {status}
              </Typography>
            </Box>
          )}
        </Paper>

        {/* Output Tabs */}
        {(output || analystOutputs.length > 0) && (
          <Paper elevation={3} sx={{ mt: 4 }}>
            <AppBar position="static" color="default">
              <Tabs
                value={value}
                onChange={handleTabChange}
                variant="fullWidth"
                indicatorColor="primary"
                textColor="primary"
              >
                <Tab label="Manager's Output" />
                <Tab label="Analysts' Outputs" />
              </Tabs>
            </AppBar>
            {value === 0 && (
              <Box p={3}>
                <Typography variant="h4" gutterBottom>
                  Manager's Output
                </Typography>
                <Typography variant="body1" sx={{ whiteSpace: 'pre-wrap' }}>
                  {output}
                </Typography>
              </Box>
            )}
            {value === 1 && (
              <Box p={3}>
                <Typography variant="h4" gutterBottom>
                  Analysts' Outputs
                </Typography>
                {analystOutputs.map((analystOutput) => (
                  <Paper key={analystOutput.analystId} sx={{ p: 2, mb: 2 }}>
                    <Typography variant="h6">
                      Analyst {analystOutput.index}
                    </Typography>
                    <Typography variant="body1" sx={{ whiteSpace: 'pre-wrap' }}>
                      {analystOutput.output}
                    </Typography>
                  </Paper>
                ))}
              </Box>
            )}
          </Paper>
        )}
      </Container>
    </ThemeProvider>
  );
}

export default App;
